exports.addHPD = addHPD;
window.addHPD = addHPD;
/**
 * Decrement defense health point.
 */
function redHPD() {
    client.sendHP(1, client.hp[0] - 1);
}
exports.redHPD = redHPD;
window.redHPD = redHPD;
/**
 * Increment prosecution health point.
 */
function addHPP() {
    client.sendHP(2, client.hp[1] + 1);
}
exports.addHPP = addHPP;
window.addHPP = addHPP;
/**
 * Decrement prosecution health point.
 */
function redHPP() {
    client.sendHP(2, client.hp[1] - 1);
}
exports.redHPP = redHPP;
window.redHPP = redHPP;
/**
 * Update background preview.
 */
function updateBackgroundPreview() {
    var background_select = (document.getElementById("bg_select"));
    var background_filename = (document.getElementById("bg_filename"));
    var background_preview = (document.getElementById("bg_preview"));
    if (background_select.selectedIndex === 0) {
        background_filename.style.display = "initial";
        background_preview.src = "".concat(exports.AO_HOST, "background/").concat(encodeURI(background_filename.value.toLowerCase()), "/defenseempty.png");
    }
    else {
        background_filename.style.display = "none";
        background_preview.src = "".concat(exports.AO_HOST, "background/").concat(encodeURI(background_select.value.toLowerCase()), "/defenseempty.png");
    }
}
exports.updateBackgroundPreview = updateBackgroundPreview;
window.updateBackgroundPreview = updateBackgroundPreview;
/**
 * Highlights and selects a menu.
 * @param {number} menu the menu to be selected
 */
function toggleMenu(menu) {
    if (menu !== selectedMenu) {
        document.getElementById("menu_".concat(menu)).className = "menu_button active";
        document.getElementById("content_".concat(menu)).className =
            "menu_content active";
        document.getElementById("menu_".concat(selectedMenu)).className = "menu_button";
        document.getElementById("content_".concat(selectedMenu)).className =
            "menu_content";
        selectedMenu = menu;
    }
}
exports.toggleMenu = toggleMenu;
window.toggleMenu = toggleMenu;
/**
 * Highlights and selects a shout for in-character chat.
 * If the same shout button is selected, then the shout is canceled.
 * @param {number} shout the new shout to be selected
 */
function toggleShout(shout) {
    if (shout === selectedShout) {
        document.getElementById("button_".concat(shout)).className = "client_button";
        selectedShout = 0;
    }
    else {
        document.getElementById("button_".concat(shout)).className = "client_button dark";
        if (selectedShout) {
            document.getElementById("button_".concat(selectedShout)).className =
                "client_button";
        }
        selectedShout = shout;
    }
}
exports.toggleShout = toggleShout;
window.toggleShout = toggleShout;
exports.default = Client;
